.recap__gridContainer___1qvOl{
    display: grid;
    grid-template-columns: 2fr 2fr 2fr 2fr 1fr;
    grid-template-rows: 1fr 3fr 3fr;
    grid-template-areas: 
                        "header header header header buttons"
                        "composite composite composite composite buttons"
                        "english math reading science buttons";
    grid-row-gap: 7px;
    row-gap: 7px;
    grid-column-gap: 7px;
    -webkit-column-gap: 7px;
            column-gap: 7px;
    height: 98vh;
    width: 98vw;
    margin: 1rem;
}

.recap__header___3LQn9{
    height: 15vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: linear-gradient(#102e79,#0d2561);
    grid-area: header;
    color: white;
    font-size: 6rem;
}

.recap__header___3LQn9 > img {
    height: 87%;
    margin: 1rem 4rem 1rem 1rem;
}

.recap__header___3LQn9 > h1{
    font-weight: normal;
    margin: 0 0 0 3rem;
}

.recap__composite___1-I2w{
    background-color: rgb(230, 230, 230);
    border: 5px solid #102e79;
    grid-area: composite;
}

.recap__buttons___3Aio3{
    background-image: linear-gradient(#102e79,#0d2561);
    grid-area: buttons;
}

.recap__section___27kIJ{
    background-color: rgb(230, 230, 230);
}

.recap__sectionHeader___1Q2Wb{
    top: 0;
    background-image: linear-gradient(#102e79,#0d2561);;
    padding: 1rem;
    text-align: center;
}

.recap__sectionTitle___1fhV6{
    display: inline-block;
    color: white;
    font-size: 4rem;
}

.recap__flexContainer___ob6VM{
    display: flex;
    flex-wrap: wrap;
    height: 80%;
    width: 100%;
    align-items: center;
    justify-content: center;
}

.recap__button___1RhQy {
    margin: 1rem;
    font-family: 'Open Sans';
    border: 1px rgb(223, 223, 223) solid;
    font-size: 1.3rem;
    font-weight: bold;
    min-height: 4rem;
    width: 8vw;
    border-radius: 1rem;
    background-image: linear-gradient(#FDFDFD,rgb(223,223,223));
    color:rgb(36, 36, 36);
}

.recap__button___1RhQy:hover {
    cursor: pointer;
    background-image:none;
    background-color: #FDFDFD;
    color: rgb(36, 36, 36)
}

.recap__english___1xQdh{
