}

.recap__math___sTwJB{
    grid-area: math;
}

.recap__reading___2q_0F{
    grid-area: reading;
}

.recap__science___nG5XO{
    grid-area: science;
}

.recap__selected___10VIf, .recap__selected___10VIf:hover {
    background-image: linear-gradient(#102e79,#0d2561);
    color: #FDFDFD;
    border: solid white 2px;
}
