.scienceSummary__gridContainer___3vZH3{
    display: grid;
    grid-template-columns: 2fr 2fr 2fr 2fr 1fr;
    grid-template-rows: 1fr 2fr;
    grid-template-areas: 
                        "composite composite composite composite buttons"
                        "questionTypes general general timeManagement buttons";
    grid-row-gap: 7px;
    row-gap: 7px;
    grid-column-gap: 7px;
    -webkit-column-gap: 7px;
            column-gap: 7px;
    height: 98vh;
    width: 98vw;
    margin: 1rem;
}

.scienceSummary__header___ljBAV{
    height: 15vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: linear-gradient(#102e79,#0d2561);
    grid-area: header;
    color: white;
    font-size: 6rem;
}

.scienceSummary__header___ljBAV > img {
    height: 87%;
    margin: 1rem 4rem 1rem 1rem;
}

.scienceSummary__header___ljBAV > h1{
    font-weight: normal;
    margin: 0 0 0 3rem;
}

.scienceSummary__composite___13ku-{
    background-color: rgb(230, 230, 230);
    border: 5px solid #102e79;
    grid-area: composite;
}

.scienceSummary__buttons___1kklW{
    background-image: linear-gradient(#102e79,#0d2561);
    grid-area: buttons;
}

.scienceSummary__section___LUAl4{
    background-color: rgb(230, 230, 230);
}

.scienceSummary__sectionHeader___3gGKQ{
    background-image: linear-gradient(#102e79,#0d2561);
    padding: 1rem;
    text-align: center;
    min-height: 12%;
}

.scienceSummary__sectionTitle___3RHJn{
    display: inline-block;
    color: white;
    font-size: 3rem;
}

.scienceSummary__flexContainer___2qJwE{
    display: flex;
    flex-wrap: wrap;
    height: 80%;
    width: 100%;
    align-items: center;
    justify-content: center;
}

.scienceSummary__button___3bQEx {
    margin: 1rem;
    font-family: 'Open Sans';
    border: 1px rgb(223, 223, 223) solid;
    font-size: 1.3rem;
    font-weight: bold;
    min-height: 4rem;
    width: 8vw;
    border-radius: 1rem;
    background-image: linear-gradient(#FDFDFD,rgb(223,223,223));
    color:rgb(36, 36, 36);
}

.scienceSummary__button___3bQEx:hover {
    cursor: pointer;
    background-image:none;
    background-color: #FDFDFD;
    color: rgb(36, 36, 36)
}

.scienceSummary__questionTypes___26p4v{
    grid-area: questionTypes;
}

.scienceSummary__general___3Rbqg{
    grid-area: general;
}

.scienceSummary__timeManagement___2_9PT{
    grid-area: timeManagement;
}

.scienceSummary__selected___16u3B, .scienceSummary__selected___16u3B:hover {
    background-image: linear-gradient(#102e79,#0d2561);
    color: #FDFDFD;
    border: solid white 2px;
}

.scienceSummary__errorsContainer___3ScWL{
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    flex-direction: column;
    border: 4px solid #102e79;
    color: #102e79;
    font-size: 2rem;
    padding: 1rem 3.5rem;
    height: 88%;
    justify-content: space-around;
    flex-grow: 1;
}

.scienceSummary__errItem___1rGpo{
    display: flex;
    justify-content: space-between;
}

.scienceSummary__errType___bxORr {
    font-weight: bold;
}

.scienceSummary__noErrors___wVkkB {
    font-size: 6rem;
    font-weight: bold;
    color: #04B14D;
    align-self: center;
    justify-self: center;
}

.scienceSummary__typeFeedback___3yPTL{
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    flex-direction: row;
    border: 4px solid #102e79;
    color: #102e79;
    font-size: 2rem;
    padding: 0;
    height: 88%;
    justify-content: space-around;
    flex-grow: 1;
}